#slide-bar {
    width: calc((100% - 10px) / 3);
    min-width: 300px;
    max-width: 900px;
    height: calc(100%);
    flex-grow: 0;
    background-color: rgb(37, 37, 37); 
    padding: 20px;
    margin-right: 5px;
    /* add when resize */
}
#slide-bar-mobile {
    flex-grow: 0;
    width: 100%;
    min-width: 300px;
    max-width: 900px;
    height: 100%;
    background-color: rgb(37, 37, 37); 
    padding: 20px;
    margin-right: 5px;
    position: absolute;
}


.change-content {
  border-radius: 10px;
  height: calc(100% - 50px - 45px);
  width: 100%;
  overflow: hidden;
  position: relative;
  background-color: rgb(37, 37, 37);
}
.change-content > .active {
  -webkit-animation: showupbox 0.2s ease forwards;
          animation: showupbox 0.2s ease forwards;
}
@-webkit-keyframes showupbox {
  0% {
    z-index: 4;
    top: 100%;
  }
  100% {
    z-index: 4;
    top: 0%;
  }
}
@keyframes showupbox {
  0% {
    z-index: 4;
    top: 100%;
  }
  100% {
    z-index: 4;
    top: 0%;
  }
}
