.dialog {
    box-shadow: 10px 10px 18px -6px rgba(222, 219, 219, 0.75);
    -webkit-box-shadow: 10px 10px 18px -6px rgba(222, 219, 219, 0.75);
    -moz-box-shadow: 10px 10px 18px -6px rgba(222, 219, 219, 0.75);
    background-color: rgb(90, 90, 90);
    width: 100%;
    max-width: 800px;
    height: 100%;
    max-height: 800px;
    position: absolute;
    color: white;
    z-index: 999;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 30px;
    padding: 40px 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.dialog-active {
    display: flex;
    -webkit-animation: show-animation 0.5s ease forwards;
            animation: show-animation 0.5s ease forwards;
}

.dialog-hide {
    display: none;
    opacity: 0;
}

.dialog .exist-btn {
    text-align: center;
    cursor: pointer;
    font-size: 30px;
    width: 35px;
    height: 35px;
    position: absolute;
    transition: color, background-color 0.2s ease;
    color: rgb(172, 172, 172);
    background-color: rgb(148, 135, 135);
    border-radius: 50%;
    top: 6px;
    right: 10px;
}

.dialog .exist-btn:hover {
    color: rgb(255, 255, 255);
    background-color: rgb(250, 11, 11);
}
@-webkit-keyframes show-animation {
    0% {
        -webkit-transform:translate(-50%, -50%) scale(0.5);
                transform:translate(-50%, -50%) scale(0.5);
        opacity: 0;
    }
    60% {
        -webkit-transform: translate(-50%, -50%) scale(1.2);
                transform: translate(-50%, -50%) scale(1.2);
        opacity: 0.8;
    }
    80% {
        -webkit-transform: translate(-50%, -50%) scale(1.05);
                transform: translate(-50%, -50%) scale(1.05);
        opacity: 0.8;
    }
    100% {
        -webkit-transform:translate(-50%, -50%) scale(1);
                transform:translate(-50%, -50%) scale(1);
        opacity: 1;
    }
}
@keyframes show-animation {
    0% {
        -webkit-transform:translate(-50%, -50%) scale(0.5);
                transform:translate(-50%, -50%) scale(0.5);
        opacity: 0;
    }
    60% {
        -webkit-transform: translate(-50%, -50%) scale(1.2);
                transform: translate(-50%, -50%) scale(1.2);
        opacity: 0.8;
    }
    80% {
        -webkit-transform: translate(-50%, -50%) scale(1.05);
                transform: translate(-50%, -50%) scale(1.05);
        opacity: 0.8;
    }
    100% {
        -webkit-transform:translate(-50%, -50%) scale(1);
                transform:translate(-50%, -50%) scale(1);
        opacity: 1;
    }
}

